import classNames from "classnames";
import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage, getImage} from "gatsby-plugin-image"
import React from 'react'

export const img = React.memo<any>((props: any) => {
    const data = useStaticQuery(graphql`
        query {
            allImageSharp {
                nodes {
                    gatsbyImageData(width: 900)
                    fluid {
                        originalName
                    }
                }
            }
        }
    `)

    const imageName = props.src.substring(2);
    const { gatsbyImageData: image } = data.allImageSharp.nodes.find((x: any) => x.fluid.originalName == imageName);

    return <div className={ classNames("text-center", "g-mb-30") }>
        <GatsbyImage className={ classNames("img-fluid", "u-shadow-v2", "g-rounded-3", "g-mb-10") }
                     image={getImage(image)}
                     alt={ props.alt }/>
        <em className={ classNames("g-color-gray-light-v1") }>{ props.alt }</em>
    </div>
});
