import classNames from "classnames";
import React from 'react'

export const blockquote = (props: any) => {
    const { children } = props;

    function multiBlockQuote() {

        return children.map(({ props }: any, index: number) => {

            if ( typeof props.children === 'string' || Array.isArray(props.children) ) {
                return <p className={ "g-mb-0" } key={ index }>
                    { props.children }
                </p>
            }

            if ( props.children.props.originalType === "em" ||
                props.children.props.originalType === "a" ) {
                return <p className={ classNames("g-mb-0", 'text-align-end') } key={ index }>
                     { props.children }
                </p>
            }

            return <h3 className={ classNames("h3", "g-front-weight-300", "g-mb-20") }>
                { props.children }
            </h3>
        });
    }

    return <div
        className={ classNames("g-brd-blue-left", "u-shadow-v5", "g-bg-white", "g-brd-around", "g-brd-gray-light-v4", "g-line-height-2", "g-pa-40", "g-mb-30") }>
        {
            Array.isArray(children) ?
            multiBlockQuote() :
            <p className={ "g-mb-0" }>
                { children.props.children }
            </p>
        }
    </div>
}

