import React from "react";
import {MDXRenderer} from "gatsby-plugin-mdx"
import {graphql, Link} from "gatsby";
import Layout from "../component/layout";
import {MDXProvider} from '@mdx-js/react'
import {connponents} from "../component/posts";
import {DiscussionEmbed} from 'disqus-react';
import classNames from "classnames";
import Tags from "../component/tags";

const Posts: React.FunctionComponent<any> = (props: any) => {
    return <Layout title={props.data.mdx.frontmatter.title} description={props.data.mdx.frontmatter.description}>
        <div className={classNames('g-mb-30')}>
            <p className={classNames('d-block', 'g-color-gray-dark-v4', 'g-font-weight-700', 'g-font-size-12', 'text-uppercase', 'mb-2')}>
                {props.data.mdx.frontmatter.date}
            </p>
            <MDXProvider components={connponents}>
                <connponents.h1>
                    {props.data.mdx.frontmatter.title}
                </connponents.h1>
                <MDXRenderer>{props.data.mdx.body}</MDXRenderer>
            </MDXProvider>
        </div>

        <div className={classNames('u-heading-v2-6--bottom', 'g-mb-30', 'g-mt-30')}>
            <h3 className={classNames('h3', 'mb-0')}>
                
            </h3>
        </div>
        <Tags tags={props.data.mdx.frontmatter.tags} className={classNames('g-mb-40--lg', 'g-mb-30--md')} />

        <DiscussionEmbed
            shortname={"yi-shiuan"}
            config={
                {
                    url: `https://yi-shiuan.github.io${props.data.mdx.fields.path}`,
                    identifier: props.data.mdx.id,
                    title: props.data.mdx.frontmatter.title,
                    language: 'zh_TW'
                }
            }
        />
    </Layout>
}

export const pageQuery = graphql`
    query($id: String) {
        mdx(id: { eq: $id }) {
            id
            body
            frontmatter {
                title
                description
                date(formatString: "DD MMM YYYY")
                tags
            }
            fields {
                path
            }
        }
    }
`

export default Posts;
