import React from 'react'
import classNames from "classnames";

export const link = (props: any) => {
    const { href,
        children
    } = props;
    return <a
        className={classNames("u-link-v5", "g-color-blue", "rounded", "g-py-8", "g-color-purple--hover")}
        href={href} target="_blank">
        {children}
    </a>
}
